.MainHeader {
  width: 100%;
  height: 80px;
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  justify-content: space-between;
  background-color: rgb(12, 12, 17);
  box-shadow: 0px 0px 8px #1e1c25;
  box-sizing: border-box;
  text-align: center;
  font-size: 35px;
  z-index: 100;
}

svg {
  margin: 10px 10px;
  fill: white;
  height: 60px;
  width: 60px;
  cursor: pointer;
  transition: 0.5s ease-in;
}

svg:hover {
  fill: red;
  transform: rotate(180deg);
}

.LinkDiv {
  margin: 10px 10px;

  display: flex;
  justify-content: space-between;
}

.Title {
  text-align: center;
  margin: 13px;
  margin-right: -30px;
}
@media (max-width: 500px) {
  .MainHeader {
    width: 100%;
    top: 0;
    left: 0;
    position: fixed;
    background-color: rgb(12, 12, 17);
    box-shadow: 0px 0px 8px #1e1c25;
    box-sizing: border-box;
    text-align: center;
    font-size: 35px;
    z-index: 100;
    margin-bottom: 30px;
  }
  .Title {
    display: none;
  }
}
