.Filter {
  margin-top: 80px;
  display: flex;
  padding: 8px 10px;
  justify-content: space-evenly;
  align-items: center;
  position: fixed;
  top: 0;
  background-color: rgb(12, 12, 17);
  box-shadow: 0px 0px 8px #1e1c25;
  box-sizing: border-box;
  width: 100%;
}
@media (max-width: 500px) {
  .Filter {
    margin-top: 80px;
    display: flex;
    flex-flow: column;
    padding: 8px 10px;
    justify-content: space-evenly;
    align-items: center;
    top: 0;
    background-color: rgb(12, 12, 17);
    box-shadow: 0px 0px 8px #1e1c25;
    box-sizing: border-box;
    width: 100%;
    position: inherit;
  }
}
