.Modal {
    position: fixed;
    z-index: 500;
    background-color: rgb(26, 26, 34);
    width: 70%;
    box-shadow: 1px 1px 1px #1e1c25;
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    border-radius: 10px;
    transition: all 0.4s ease-out;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}