.TweetRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 0px;
  margin: 10px;
}

@media (max-width: 500px) {
  .TweetRow {
    width: auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-flow: column;
    padding: 10px 0px;
    margin-bottom: 10px;
  }
}
