.VaccModal {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-evenly;
}
.TypeAhead {
  width: 190px;
}
.ButtonGroup {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.StateInput {
  color: white;
  padding: 5px 10px;
  border: 1px solid #ced4da;
  border-radius: 3px;
  background-color: black;
}
.StateInput:active {
  color: white;
  border: 1px solid #ced4da;
  background-color: black;
}
.StateInput::placeholder {
  color: #495057;
}
.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  color: #5c9210;
  font-weight: bold;
}

.Button:first-of-type{
  color: orange;
}

.StateClass {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.StateClass p {
  margin: 10px 10px;
}
