.TweetPanel {
  text-align: center;
  position: inherit;
  margin-right: 20px;
  margin-top: 180px;
}

@media (max-width: 500px) {
  .TweetPanel {
    margin-top: 0px;
  }
}

.dataLoader {
  font-size: 30px;
}
.lds_ripple {
  text-align: center;
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds_ripple div {
  position: absolute;
  border: 4px solid rgb(55, 54, 87);
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds_ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
